import React, { useState } from 'react';
import Header from '../Headers/Header';
import LoginHeader from '../Headers/LoginHeader';
import Footer from '../Footer/Footer';
import { useForm } from "react-hook-form";
import axios from 'axios'

const Otpform = () => {

    const [inputField, setInputField] = useState({})
    const { register, handleSubmit, setValue, formState: { errors } } = useForm(
        {
            criteriaMode: 'firstError',
            mode: 'onSubmit',
            reValidateMode: 'onChange'
        }
    );
    //handle submit function
    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        if(value.length <= 1){  
            setInputField(values => ({ ...values, [name]: value }))
        }
    }
    const onSubmit = (data) => {
        const values = ''
        const getToken = values.concat(data.firstField, data.secondField, data.thirdField, data.fourthField, data.fifthField, data.sixthField)
        alert(getToken)
        axios({
            method: 'post',
            url: `http://localhost:4000/otp-verification`,
            headers: { 'content-type': 'application/json' },
            data : getToken
          }).then(function (response) {
             console.log(response)
          })
            .catch(function (error) {
              console.log('Something goes wrong');
            });
    }

    return (
        <>
            <LoginHeader />
            <div class="app-content page-body">
                <div class="container height-100 d-flex justify-content-center align-items-center">
                    <div class="position-relative">
                        <div class="card-one p-2 text-center">
                            <form onSubmit={handleSubmit(onSubmit)} class="center-div">
                                <h6>Please enter the one time password <br /> to verify your account</h6>
                                <div> <span>A code has been sent to</span> <small>*******9897</small> </div>
                                <div id="otp" class="inputs d-flex flex-row justify-content-center mt-2">
                                    <input class="m-2 text-center form-control rounded" type="number" id="first"  name='firstField' value={inputField.firstField || ""}
                                        {...register("firstField", { required: true, onChange: (e) => {handleChange(e)} })} />
                                    <input class="m-2 text-center form-control rounded" type="number" id="second" name='secondField' value={inputField.secondField || ""}
                                        {...register("secondField", { required: true, onChange: (e) => {handleChange(e)}})} />
                                    <input class="m-2 text-center form-control rounded" type="number" id="third"  name='thirdField' value={inputField.thirdField || ""}
                                        {...register("thirdField", { required: true, onChange: (e) => {handleChange(e)} })} />
                                    <input class="m-2 text-center form-control rounded" type="number" id="fourth" name='fourthField' value={inputField.fourthField || ""}
                                        {...register("fourthField", { required: true, onChange: (e) => {handleChange(e)} })} />
                                    <input class="m-2 text-center form-control rounded" type="number" id="fifth"  name='fifthField' value={inputField.fifthField || ""}
                                        {...register("fifthField", { required: true, onChange: (e) => {handleChange(e)} })} />
                                    <input class="m-2 text-center form-control rounded" type="number" id="sixth"  name='sixthField' value={inputField.sixthField || ""}
                                        {...register("sixthField", { required: true, onChange: (e) => {handleChange(e)} })} />
                                </div>
                                <div class="mt-4">
                                    <button type='submit' class="btn btn-danger px-4 validate">Validate</button>
                                </div>
                            </form>
                            <div class="card-2">
                                <div class="content d-flex justify-content-center align-items-center"> <span>Didn't get the code</span> <a href="#" class="text-decoration-none ms-3">Resend Code</a> </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    );
}

export default Otpform;